.container {
    max-width: 400px;
}

.avatar {

}

.avatar img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.message-row {
    margin-bottom: 5px;
}

.message-author {
    font-weight: bold;
}

.message-time {
    color: gray;
    font-size: smaller;
}

.remove-btn {
    visibility: hidden;
    padding: 0;
    color: lightgray;
    font-size: 12px;
    margin: 0;
}

.remove-btn:hover {
    color: red;
    text-decoration: none;
}

.message-row:hover .remove-btn {
    visibility: visible;
}

.fixed-row {
    position: fixed;
    bottom: 0;
    width: 400px;
    max-width: 100%;
}

.messages {
    margin-top: 35px;
    margin-bottom: 30px;
}

.header {
    position: fixed;
    top: 0;
    z-index: 2;

    width: 400px;
    max-width: 100%;
    padding: 4px;

    background-color: white;
    box-shadow: 0 2px 2px -1px whitesmoke;
}

.btn-switch-theme {
    padding: 0;
    text-decoration: none;
}

/*dark theme*/
.dark-theme {
    background-color: #022c43;
    color: whitesmoke;
}

.dark-theme .container {
    background-color: #053f5e;
}

.dark-theme .header {
    background-color: #115173;
    box-shadow: none;
}

.dark-theme .fixed-row {
    background-color: #053f5e;
}

.dark-theme .btn-switch-theme {
    color: whitesmoke;
}
